import React from "react";
import "./Blogs.scss";
import { blogSection } from "../../portfolio";

const Blogs = () => {
  if (!blogSection.display) return null;

  return (
    <div className="main" id="blogs">
      <h1 className="blog-title">{blogSection.title}</h1>
      <p className="blog-subtitle">{blogSection.subtitle}</p>
      <div className="blogs-container">
        {blogSection.blogs.map((blog, index) => (
          <div className="blog-card" key={index}>
            {blog.image && (
              <img className="blog-card-image" src={blog.image} alt={blog.title} />
            )}
            <div className="blog-card-content">
              <h3 className="blog-card-title">{blog.title}</h3>
              <p className="blog-card-desc">{blog.description}</p>
              <a
                className="blog-link"
                href={blog.url}
                target="_blank"
                rel="noopener noreferrer"
              >
                Read More →
              </a>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
};

export default Blogs;
